
#set .weui-cell:before {
  border: none;
}
#set textarea {
  display: block!important;
  width: 96%;
  height: 100px;
  box-sizing: border-box;
  padding: 6px 8px;
  margin: auto;
  border: 1px solid #ededed;
}
#set .photo {
  margin-top: 10px;
}
#set .photo .photo-img-box {
  width: 64px;
  height: 64px;
  border: 1px solid #ededed;
  margin-left: 2%;
}
#set .photo .photo-img-box img {
  width: 50px;
}



