
#contacts ul {
  width: 100%;
  position: absolute;
  top: 200px;
  bottom: 0;
  overflow: auto;
}
#contacts ul li {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  border-bottom: 1px solid lightblue;
}
#contacts ul li .name {
  -webkit-box-flex: 0;
  -webkit-flex: 0 0 100px;
          flex: 0 0 100px;
  width: 100px;
}
#contacts ul li .phone {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
          flex: 1;
}
#contacts ul li .phone p {
  line-height: 30px;
}



