/*紧急联系人关系选择*/
.relationship .weui-cell:before {
  left: 0!important;
}
.relationship .weui-cells {
  margin-top: 0.5em;
}
.relationship .relationshipCheck {
  padding: 10px 5%;
}
.relationship .relationshipCheck span {
  width: 16px;
  height: 16px;
  display: inline-block;
  vertical-align: middle;
  margin-right: 5px;
}
.relationship .relationshipCheck .relationshipNotCheck {
  width: 30%;
  text-align: center;
}
.relationship .relationshipCheck .relationshipNotCheck span {
  border: 1px solid #999;
  background-color: #fff;
}
.relationship .relationshipCheck .relationshipChecked span {
  background-color: #8fc31f;
  border: 1px solid #8fc31f;
}
.company_license_img_box {
  height: 200px;
  margin-top: 20px;
}
.company_license_img_box form,
.company_license_img_box input {
  width: 100%;
  height: 200px!important;
}
.company_license_img_box .btn-primary-img {
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -webkit-flex-flow: row;
          flex-flow: row;
}
.company_license_img_box .btn-primary-img .company_license_img {
  -webkit-box-flex: 1;
  -webkit-flex: 1;
          flex: 1;
  margin-left: 16px;
  border: 1px dashed #ddd;
}
.company_license_img_box .btn-primary-img p {
  width: 140px;
  text-align: center;
}
.company_license_img_box .company_license_img {
  background: #fff;
  height: 200px;
}
.company_license_img_box .company_license_img img {
  width: 100%;
  height: 100%;
}
.id-img .id-img-box {
  width: 260px;
  height: 160px;
  margin: 20px auto;
  background: #fff;
  box-shadow: 0 0 6px #999;
}
.id-img .id-img-box img {
  width: 100%;
  height: 100%;
}
.id-img .id-img-btn {
  background: #666;
  width: 180px;
  margin: auto;
  height: 45px;
  border-radius: 4px;
  color: #fff;
}
.bigImg {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);
}
.bigImg img {
  width: 100%;
}



