
#scroller .foot {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 45px;
  background: lightblue;
  color: #fff;
  line-height: 45px;
  text-align: center;
}
#scroller .content {
  position: absolute;
  top: 47px;
  bottom: 45px;
  width: 100%;
  z-index: 99999999;
  overflow: hidden;
}
#scroller .content ul li {
  line-height: 40px;
  color: #666;
  font-size: 18px;
  text-align: center;
  border-bottom: 1px solid #ededed;
}



