
#idCardInfo {
  font-size: 14px;
}
#idCardInfo .head-confirm-btn {
  background: #fff;
  margin: -10px;
}
#idCardInfo .mask {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.9);
}
#idCardInfo .mask img {
  width: 100%;
}
#idCardInfo .display-img {
  margin-left: 3%;
  width: 94%;
  height: 200px;
  background: #fff;
  border: 1px dotted #999;
}
#idCardInfo .display-img img {
  width: 90%;
}
#idCardInfo .core-img {
  margin: 6px auto;
  width: 200px;
  background: #8fc31f;
  font-size: 14px;
  color: #fff;
  text-align: center;
  padding: 8px 15px;
  border-radius: 6px;
}



