
.foot-confirm-btn {
  position: fixed;
  bottom: 0;
  left: 0;
  background: #8fc31f;
  color: #fff;
}
#uploadInfo .core-image-box {
  margin-top: 10px;
  width: 100%;
  height: 180px;
  padding: 8px 0;
}
#uploadInfo .core-image-box .display-img {
  -webkit-box-flex: 0;
  -webkit-flex: 0 0 140px;
          flex: 0 0 140px;
  width: 140px;
  height: 100%;
  border: 1px dotted #999;
  background: #fff;
  margin-right: 8px;
}
#uploadInfo .core-image-box .display-img img {
  width: 100%;
  height: 100%;
  box-shadow: 0 0 10px #4d4d4d;
}
#uploadInfo .core-image-box .core-img {
  -webkit-box-flex: 0!important;
  -webkit-flex: 0 0 120px!important;
          flex: 0 0 120px!important;
  width: 120px!important;
  padding: 70px 12px;
  text-decoration: underline;
}
#uploadInfo .core-image-box .core-img form {
  height: 100%!important;
}
#uploadInfo .bin-img {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.8);
}
#uploadInfo .bin-img img {
  width: 99%;
}



