
#showdeposit .createdTime {
  height: 40px;
  background: #cce0a7;
  color: #fff;
}
#showdeposit .underline {
  text-decoration: underline;
  color: #8fc31f;
}
#showdeposit .businessPopup {
  width: 100%;
  height: 100%;
  background: #fff;
  overflow-y: auto;
  position: relative;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-flow: column;
          flex-flow: column;
}
#showdeposit .businessPopup ._close {
  position: absolute;
  height: 40px;
  width: 40px;
  top: 0;
  right: 0;
  text-align: center;
  line-height: 40px;
}
#showdeposit img {
  width: 100%;
  padding: 4px 0;
}



