
#openzm .weui-cells:before {
  display: none;
}
#openzm .weui-cell:before {
  left: 0!important;
}
#openzm .vux-header {
  background: #8fc31f!important;
}
#openzm .vux-header .left-arrow:before {
  border: 1px solid #fff;
  border-width: 1px 0 0 1px;
}
#openzm .vux-header .vux-header-title,
#openzm .vux-header .vux-header-right {
  color: #fff!important;
}
#openzm .zm-logo img {
  width: 36%;
  padding: 30px 0 35px;
}
#openzm .zm-describe {
  font-size: 14px;
  color: #999;
}



